export enum Orientation {
    LANDSCAPE = "landscape",
    PORTRAIT = "portrait"
}

export enum ScaleMode {
    FIT = "fit",
    FILL = "fill",
    STRETCH = "stretch"
}

export enum FillMode {
    /** Scale by width - fills width, may crop top/bottom */
    WIDTH = "width",
    /** Scale by height - fills height, may crop left/right */
    HEIGHT = "height",
    /** Auto - uses Math.max to fill entire screen (current behavior) */
    AUTO = "auto"
}

export type RendererBackground =
    | { type: "color"; value: number | string }
    | { type: "image"; src: string };

export interface IGameConfig {
    /**
     *     
     *  ,     
     */
    logicWidth: number;

    /**
     *     
     */
    logicHeight: number;

    /**
     *  
     */
    orientation: Orientation;

    /**
     *  
     */
    scaleMode: ScaleMode;

    /**
     *    ScaleMode.FILL
     * - WIDTH:    (  /)
     * - HEIGHT:    (  /)
     * - AUTO:  (Math.max -   )
     */
    fillMode?: FillMode;

    /**
     * Renderer settings
     */
    renderer: {
        /**
         *   (hex)
         */
        background?: RendererBackground;

        /**
         *  
         *  ,    FPS
         */
        antialias: boolean;

        /**
         *  device pixel ratio
         *  resolution   
         */
        maxResolution: number;

        /**
         * Auto density -   devicePixelRatio
         */
        autoDensity: boolean;

        /**
         * Auto start ticker
         */
        autoStart: boolean;
    };

    /**
     * CSS   game container
     *  null -   
     *   -    (  )
     */
    container: {
        /**
         *   (CSS)
         * : "100vw", "800px", "80%"
         * null = 
         */
        width: string | null;

        /**
         *   (CSS)
         * : "100vh", "600px", "90%"
         * null = 
         */
        height: string | null;

        /**
         * CSS background  
         */
        cssBackground: RendererBackground;

        /**
         * Border radius  
         */
        borderRadius: string;

        /**
         *  
         */
        position: "relative" | "absolute" | "fixed";

        /**
         *  true -   100% viewport
         */
        fullscreen: boolean;
    };

    /**
     * Debug 
     */
    debug: {
        /**
         *  FPS
         */
        showFPS: boolean;

        /**
         *  debug  
         */
        showCoordinatePoints: boolean;

        /**
         *    
         */
        verbose: boolean;
    };
}



